@import "src/assets/sass/variables";

.projectBox {
  display: flex;
  width: 100%;
  padding: 0 30px;
  justify-content: space-around;
  flex-wrap: wrap;
}

.tiles {
  width: 450px;
  margin-bottom: 20px;

  h1 {
    margin: 0 0 5px 0;
    padding: 0;
    text-align: center;
    color: $accentText;
  }

  p {
    margin: 0;
    padding:5px 0;
    font-size: 17px;
    color: $accentText;
    text-align: justify;

    &:first-letter {
      padding-left: 20px;
    }

  }

  .tile {
    display: inline-block;
    margin: 10px;
    text-align: left;
    opacity: .99;
    overflow: hidden;
    position: relative;
    border-radius: 20px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.05);
    border: 3px solid $primaryText;
    transition: all 0.3s linear;

    &:hover {
      border: 3px solid $accentText;
      transition: all 0.5s linear;
    }
  ;

    img {
      display: block;
      max-width: 100%;
      backface-visibility: hidden;
    }

    .details {
      font-size: 16px;
      padding: 20px;
      color: #fff;
      position: absolute;
      bottom: 0;
      left: 0;

      span {
        display: block;
        opacity: 0;
        position: relative;
        top: 100px;
        transition-property: top, opacity;
        transition-duration: 0.3s;
        transition-delay: 0s;
      }

      .title {
        line-height: 1;
        font-weight: 600;
        font-size: 25px;
        margin-bottom: 15px;
        color: #ffb400;
      }

    }

  }

}

.projectImage {
  border-radius: 20px;
  width: 450px;
  height: 250px;
  background-size: cover;
}

.details {
  width: 100%;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
}

.link {
  margin-left: 5px;
  font-size: 15px;
  text-decoration: none;
  color: white;
}

.icon {
  color: #ffb400;
}

.info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.link:hover {
  color: #ffb400;
  transition: 0.3ms;
}

.tiles .tile .details .info {
  line-height: 1.2;
  margin-top: 5px;
  font-size: 12px;
  opacity: 1;
}

.tiles .tile:focus:before,
.tiles .tile:focus span,
.tiles .tile:hover:before,
.tiles .tile:hover span {
  opacity: 1;
}

.tiles .tile:focus:before,
.tiles .tile:focus span,
.tiles .tile:hover:before,
.tiles .tile:hover .details {
  background: rgba(17, 16, 16, 0.8);
}

.tiles .tile:focus:before,
.tiles .tile:hover:before {
  top: 50%;
}

.tiles .tile:focus span,
.tiles .tile:hover span {
  top: 0;
}

.tiles .tile:focus .title,
.tiles .tile:hover .title {
  transition-delay: 0.15s;
}

.tiles .tile:focus .info,
.tiles .tile:hover .info {
  transition-delay: 0.35s;
}

@media (max-width: 1200px) {
  .projectBox {
    flex-direction: column;
    align-items: center;
  }

  .tiles {
    width: 400px;
  }
}


@media (max-width: 600px) {
  .tiles {
    width: 320px;
    margin-bottom: 10px;
  }

  .projectImage {
    width: 320px;
    height: 180px;
  }

  .projectBox {
    padding: 0;
    flex-direction: row;
  }
}
